/**************** 以下、ハンバーガーボタンのスタイリング ****************/
.c-globalNav {

    &.is-open {
        display: block;
        animation: fade-in .5s ease-in-out;
    }

    @keyframes fade-in {
        0% {
            opacity: 0;
        }
    
        100% {
            opacity: 1;
        }
    }
}